<template>
    <div>
        <label v-if="label">{{ label }}</label>
        <slot></slot>
        <p v-if="error">{{error}}</p>
        <!-- <p>{{ model }}</p> -->
        <!-- <p>{{ rules[prop] }}</p> -->
    </div>
</template>
<script>

import Validator from 'async-validator'

export default {
    name: 'KFormItem',
    inject: ['model', 'rules'],
    props: {
        label: {
            type: String,
            default: ''
        },
        prop: String
    },
    mounted() {
        this.$on('validate', () => {
            this.validate()
        })
    },
    data(){
        return {
            error: ''
        }
    },
    methods: {
        validate(){
            // å è½½ä¸ä¸ªæ ¡éªåºï¼async-validator
            // 1. è·åæ ¡éªè§ååå¼
            const rules = this.rules[this.prop]
            const value = this.model[this.prop]

            // 2. è·å¾æ ¡éªå¨å®ä¾
            const validator = new Validator({[this.prop]: rules})
            return validator.validate({[this.prop]: value}, (errors) => {
                if(errors){
                    this.error = errors[0].message
                }else{
                    this.error = ''
                }
            })

        }
    }
}
</script>